@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
    font-family: 'alma-light';
    src: local('alma-light'), url(/static/media/almamono-light-webfont.1310ef26.woff) format('woff');
}

* {
    box-sizing: border-box;
    margin: 0;
    font-family: 'Montserrat';
    font-weight: 500;
}

.container {
    max-width: unset ;
}

.container-section {
    width: 60%;
}

#container-holder-about {
    background-color: #e3e3e3;
}

#container-holder-experience {
    background-color: #e3e3e3;
}

#container-holder-projects {
    background-color: white;
}

#container-holder-contact {
    background-color: #e3e3e3;
}

#container-holder-footer {
    background-color: black;
}

@media only screen and (max-width: 1024px) {
    .container-section {
        width: 90%;
    }
  }
.nav-link {
    color: white !important;
    font-family: alma-light;
}

@media only screen and (min-width: 640px) {
    .navbar {
        justify-content: center !important;
    }

    #arman-nav {
        color: #ebb734 !important;
        font-weight: bolder;
        font-size: 1.1em;
    }

    .nav-link {
        font-size: 1.0em;
    }
  }
.paral {
    height: 100vh;
    width: 100%;
}

.backgroundImage {
    height: 100vh;
    width: 100%;
}

#containerDiv {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.portraitDiv {
    text-align: center;
}

.armanBg {
    width: 250px;
    max-width: 100%;
    border: 10px solid white;
}

#containerDiv h3 {
    text-align: center;
    color: white;
    font-size: 60px;
    font-weight: 900;
}

h5 {
    text-align: center;
    color: white;
    font-size: 40px;
}



#about {
    padding: 75px 0;
    background-color: #e3e3e3;
}

@media only screen and (max-width: 640px) {
    #about {
        font-size: 0.9rem;
    }
  }

#about p {
    font-size: 1.0em;
}

#about h1 {
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    padding-bottom: 10px;
}

#about h3 {
    font-size: 1.5rem;
    font-weight: bolder;
    color: black;
    
}

.about-columns-main h4 {
    font-size: 1.2rem;
    font-weight: bolder;
    color: black;
}

#underlined {
    background-color: gray;
    margin-left: auto;
    margin-right: auto;
    width: 300px;
    max-width: 100%;
    height: 2px;
    margin-bottom: 75px;
}

@media only screen and (min-width: 640px) {
    .about-columns-main {
        padding: 0 30px 0 30px;
    }
  }

#mypic {
    width: 350px !important;
    height: 100%;
    max-width: 100%;
    border: 8px solid white;
    margin-right: auto;
    margin-left: auto;
}

#socials-icon-row {
    padding: 20px 0 20px 0;

}

#socials-icon-row > ul {
    list-style: none;
    width: 100%;
    text-align: center;
    padding: 0px;
}

#socials-icon-row > ul li{
    display: inline-block;
    padding: 0 10px 0 10px;
}

#about .socials-icon {
    width: 1.6em;
}

#about .container {
    line-height: 2.0;
}
.skills {
    padding: 75px 0;
    background-color: white;
}

h1 {
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    padding-bottom: 10px;
}

#underlined {
    background-color: gray;
    margin-left: auto;
    margin-right: auto;
    width: 300px;
    max-width: 100%;
    height: 2px;
    margin-bottom: 75px;
}

.card-zoom-in {
    transition: transform 0.3s;
}

.card-zoom-in:hover {
    transform: scale(1.05);
}

.card {
    margin-bottom: 20px !important;
}

.card-title {
    text-align: center;
    font-weight: bold;
}

.card-row {
    margin-left: auto;
    margin-right: auto;
}

.card-text {
    display: flex;
    flex-direction: column;
}

.card-span {
    padding: 0.5rem;
}

.card-a {
    text-decoration: none !important;
    color: black !important;
    font-weight: 500;
}

.card-icons {
    width: 3em;
}

.card-image {
    width: 3em;
}
#experience {
    padding: 75px 0;
    background-color: #e3e3e3;
}

@media only screen and (max-width: 640px) {
    #experience {
        font-size: 0.9rem;
    }
  }

.card-header {
    text-align: center;
}

#experience img {
    width: 250px;
    max-width: 100%;
}

#experience .buttonHolder {
    text-align: center;
}
#projects {
    padding: 75px 0;
}

@media only screen and (max-width: 640px) {
    #projects {
        font-size: 1rem;
    }
  }

#projects img {
    width: 250px;
    max-width: 100%;
    max-height: 200px;
}

#projects .btn {
    margin: 9px 0 9px 0;
}
#contact {
    padding: 75px 0;
    background-color: #e3e3e3;
}

.jumbotron {
    background-color: white;
}
#footer {
    background-color:#19142e;
    color: white;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    font-size: 1.3rem;
}
